.p{
    width: 30%;
    height: 40vh;
   margin: 20px 10px;
   border: 2px solid rgb(243, 242, 242);
   border-radius: 10px 10px 0 0;
   overflow: hidden;
}
.p-browser{
    height: 20px;
    background-color: rgb(243,242,242);
    display: flex;
align-items: center;
}
.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
   
    
}
.reed{
    background-color: red;
    border: 1px solid red;
}
.yell{
    background-color: yellow;
    border: 1px solid black;
}
.gree{
    background-color: greenyellow;
    border: 1px solid green;
}
#projects_gif{
width: 100%;
}
#git_log{
    height: 100%;

 font-size: 40px;

}
#dep_log{
height: 100%;
    font-size: 34px;
}
.source_live{
    display: flex;
    margin: auto;
    width: 20%;
    gap: 30px;
    justify-content: space-around;
    align-items: center;
    margin-top: 7px;

}
#name_url{
    font-size: 14px;
    margin-left: 5px;
}
.tech-stack{
    display: flex;
    gap: 2px;
    
}