.intro{
    display: flex;
    height: 80vh;
    margin: 6rem 6rem 0 6rem;
    gap: 170px;
    border: 1px solid red;
}
.i-left{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}
.i-name{
    display: flex;
    flex-direction: column;
}
.i-name>:nth-child(1){
    color: var(--black);
    font-weight: bold;
    font-size: 3rem;
}
.i-name>:nth-child(2){
    color: var(--orange);
    font-size: 3rem;
    font-weight: bold;
}
.i-name>:nth-child(3){
    font-weight: 100;
    font-size: 18px;
    color: var(--gray);
    margin-top: 12px;
}
.i-button{
   width: 6rem;
   height: 2rem; 
}
.i-icons{
    margin-top: -1.2rem;
    display: flex;
    gap: 0rem;
}
.i-icons img{
    transform: scale(0.5);
}
.i-icons img:hover{
    cursor: pointer;
}
#profile_pic{
    border-radius: 50%;
  width: 125%;
  height: 68%;
   
   left: 28%;
}
.blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    filter: blur(190px);
    z-index: -9;
    top: -18%;
    left: 56%;


}
.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-item{
    height: 50px;
    font-size: 25px;
    font-weight: bold;
    color: #59b256;
    display: flex;
   
}
.i-title-wrapper{
    height: 100%;
    animation: move 8s ease-in-out infinite alternate;
}
@keyframes move{
   25%{
    transform: translateY(-50px);
   }
   50%{
    transform: translateY(-100px);
   }
   75%{
    transform: translateY(-150px);
   }
   100%{
    transform: translateY(-200px);
   }
}