.a{
    height: 100vh;
    display: flex;
    align-items: center;
    margin: 0;
    border: 1px solid rebeccapurple;
}
.a-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
    }
.a-right{
    flex: 1;
}
.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 35px;
}
.a-img{
    width: 100%;
    border-radius: 10%;
    height: 100%;

}
.a-title>h1{
    font-weight: bold;
    font-size: 2rem;
}
.a-title>p{
    font-size: 22px;
}